import { AbstractControl, FormGroup } from '@angular/forms';

import { Thin2Logger } from 'thin2-log';

import { Th2GenericFormAttributesModel } from './models/th2-generic-form-attributes.model';
import { Th2GenericFormConfigModel } from './models/th2-generic-form-config.model';

export interface AllocatedFormControl {
    formControl: AbstractControl;
    config: Th2GenericFormAttributesModel;
}

export class Th2GenericFormHelper {
    public static create(
        formConfig: Th2GenericFormConfigModel,
        formGroup: FormGroup,
        logger?: Thin2Logger
    ) {
        return new Th2GenericFormHelper(formConfig, formGroup, logger);
    }

    private constructor(
        private formConfig: Th2GenericFormConfigModel,
        private formGroup: FormGroup,
        private LOGGER?: Thin2Logger
    ) {
        if (this.LOGGER) {
            this.LOGGER.debug('Initializing form helper');
        }
    }

    public allocateFormControl(formControlName: string): AllocatedFormControl {
        if (this.LOGGER) {
            this.LOGGER.debug(`Allocating control ${formControlName}`);
        }
        return this._allocateFormControl(this.formConfig, formControlName);
    }

    public allocateExternalFormControl(formControlPath: string): AbstractControl | null {
        if (this.LOGGER) {
            this.LOGGER.debug(`Allocating external control: ${formControlPath}`);
        }
        return this.formGroup.get(formControlPath);
    }

    // Forced validation of the form controls and groups
    public updateValueAndValidity() {
        if (this.formGroup && this.formGroup.controls) {
            this._updateControlValueAndValidity(this.formGroup.controls);
        }
    }

    private _allocateFormControl(
        config: any,
        formControlName: string,
        path: string[] = []
    ): AllocatedFormControl {
        let allocatedControl: AllocatedFormControl;

        let elementIdx = 0;
        let found = false;

        // Ugly loop, however this cannot be achieved cleanly with a forEach since we need to
        // stop iterating once we find an element
        while (elementIdx < config.elements.length && !found) {
            const element = config.elements[elementIdx];
            if (element.title) {
                path.push(element.name);
                allocatedControl = this._allocateFormControl(element, formControlName, path);
                // Yet another ugly workaround, tag the loop as done for in case this returns
                // a populated control
                if (allocatedControl) {
                    found = true;
                }
                path.pop();
            } else if (element.name === formControlName) {
                found = true;
                const controlPath = `${path.join('.')}.${element.name}`;
                if (this.LOGGER) {
                    this.LOGGER.debug(`Found ${formControlName} at ${controlPath}`);
                }

                allocatedControl = {
                    config: element,
                    formControl: this.formGroup.get(controlPath)
                };
            }
            elementIdx++;
        }

        return allocatedControl;
    }

    private _updateControlValueAndValidity(controlList: { [key: string]: AbstractControl }) {
        for (const key in controlList) {
            if (controlList[key]) {
                const control = controlList[key];
                if (control['controls']) {
                    this._updateControlValueAndValidity(control['controls']);
                    control.updateValueAndValidity();
                } else {
                    control.updateValueAndValidity();
                }
            }
        }
    }
}
