import { Injectable } from '@angular/core';
import { select, NgRedux } from '@angular-redux/store';
import { Observable } from 'rxjs';
import { DashboardApiDataApiActions } from './api/dashboard-api.actions';
import {
    News,
    DashboardQuery,
    Topic,
    Statistics,
    StatisticsSDGDay,
    SDGScores,
    StatisticsEntity
} from './api/dashboard-api.model';
import { AppState } from '../../store/model';

@Injectable({
    providedIn: 'root'
})
export class DashboardService {
    @select(['dashboardDataset', 'data', 'mainNews'])
    public mainNews$: Observable<News[]>;

    @select(['dashboardDataset', 'data', 'mostPolarizedNews'])
    public mostPolarizedNews$: Observable<News[]>;

    @select(['dashboardDataset', 'data', 'topics'])
    public topics$: Observable<Topic[]>;

    @select(['dashboardDataset', 'data', 'stats'])
    public stats$: Observable<Statistics[]>;

    @select(['dashboardDataset', 'data', 'statsEntity'])
    public statsEntity$: Observable<StatisticsEntity[]>;

    @select(['dashboardDataset', 'data', 'statsSDGDay'])
    public statsSDGDay$: Observable<StatisticsSDGDay[]>;

    @select(['dashboardDataset', 'data', 'sdgScores'])
    public sdgScores$: Observable<SDGScores[]>;

    constructor(
        private dashboardActions: DashboardApiDataApiActions,
        private store: NgRedux<AppState>
    ) {}

    public loadDataset(dashboardQuery: DashboardQuery) {
        this.dashboardActions.loadData(dashboardQuery); // 初始化store
        this.dashboardActions.loadTopics(dashboardQuery); // 获取Token信息
        this.dashboardActions.loadStatisticsByDay(dashboardQuery); // 获取每日统计数据
        this.dashboardActions.loadStatisticsPie(dashboardQuery); // 获取饼图统计数据
        this.dashboardActions.loadStatisticsEntity(dashboardQuery); // 获取饼图（机构）统计数据
        this.dashboardActions.loadMostPolarizedNews(dashboardQuery); // 获取最两极的新闻数据
        this.dashboardActions.loadMainNews(dashboardQuery); // 获取新闻数据
        this.dashboardActions.loadedData(); // 将store的hasDataset置为true
    }

    public loadSDGScores(dashboardQuery: DashboardQuery) {
        this.dashboardActions.loadSDGScores(dashboardQuery); // 获取新闻的全部SDG打分
    }

    // 保存最后一次的查询条件
    public saveLastFilter(lastFilter: DashboardQuery) {
        this.dashboardActions.saveLastFilter(lastFilter);
    }

    // 获取最后一次的查询条件
    public getLastFilter(): DashboardQuery {
        return this.store.getState()['dashboardDataset'].lastFilter;
    }

    // 查询store的data是否已加载过数据
    public hasDataset() {
        return this.store.getState()['dashboardDataset'].hasDataset;
    }
}
