import { Component, Input, TemplateRef } from '@angular/core';
import { BsModalRef, BsModalService } from 'ngx-bootstrap/modal';
import { DashboardService } from '../../dashboard.service';
import { News } from '../../api/dashboard-api.model';
import { EmotionalEnum } from '../emotional-emoji/emotional.enum';
import { SDGNameDictionary } from '../../../model/sdg.enum';
// import { NewsDetailComponent } from '../news-detail/news-detail.component';

@Component({
    selector: 'news-card-component',
    templateUrl: './news-card.component.html'
})
export class NewsCardComponent {
    @Input()
    public newsItem: News;
    public sdgScores$ = this.dashboardService.sdgScores$;

    private SDG: '';

    constructor(private modalService: BsModalService, private dashboardService: DashboardService) {}

    // 根据ODS的值获取相应的Logo图片
    public getImagenFrom(sdg: number): string {
        return `../../../images/sdg-images/sdg-${sdg}.png`;
    }

    // 根据新闻的情绪来决定卡片右侧的颜色
    public getRightBorderForTheNewsCard(emotion: number): { [key: string]: boolean } {
        return {
            'callout--green': emotion === EmotionalEnum.HAPPY,
            'callout--red': emotion === EmotionalEnum.SAD
        };
    }

    public viewDetail(template: TemplateRef<any>) {
        const parameters = {
            dateFrom: '',
            dateTo: '',
            sdg: [0],
            entity: [],
            news_id: this.newsItem.newsId
        };
        // console.log("(ViewDetail)Start to get scores");
        this.dashboardService.loadSDGScores(parameters);

        this.SDG = SDGNameDictionary[this.newsItem.sdgId];
        this.modalService.show(template, { class: 'modal-lg' });
    }
}
