import { Component, Input, OnChanges, SimpleChanges } from '@angular/core';
import {
    Th2NovaTableModel,
    Row
} from '../../../../utils/components/th2-nova-table/th2-nova-table-config.model';
import { Topic } from '../../api/dashboard-api.model';

@Component({
    selector: 'topics-table-component',
    templateUrl: './topics-table.component.html'
})
export class TopicsTableComponent implements OnChanges {
    @Input()
    public topics: any[]; // 输入数据Topic[]

    public currentPage = 1;
    public pageSize = 8;
    public numResults: number; // 数据总条数

    public tableConfig: Th2NovaTableModel = {
        header: {
            cells: [
                {
                    colId: 'topicNameCell',
                    title: 'Topic'
                },
                {
                    colId: 'countNewsCell',
                    title: 'News Count'
                },
                {
                    colId: 'totalNewsCell',
                    title: 'Total'
                }
            ]
        },
        body: []
    };
    private allRows = []; // 行数据

    private rowCount: number = -1; // 行序号
    private cellCount: number = -1; // 单元格序号

    public ngOnChanges(changes: SimpleChanges): void {
        this.topics = changes.topics.currentValue;
        if (this.topics) {
            this.allRows = this.topics.map(this.createRowWith.bind(this));
            this.numResults = this.allRows.length;
            this.setUpTableBody();
        }

        // console.log(this.tableConfig);
    }

    public onPageChanged(page: number) {
        this.currentPage = page;
        this.setUpTableBody();
    }

    // 将Topic转换成表格需要的行数据结构
    private createRowWith(topic: Topic): Row {
        this.rowCount += 1;
        this.cellCount += 3;
        return {
            rowId: `row-${this.rowCount}`,
            cells: [
                {
                    cellId: `cell-${this.cellCount - 3}`,
                    templateRef: 'topicName',
                    context: {
                        topicName: topic.token
                    }
                },
                {
                    cellId: `cell-${this.cellCount - 2}`,
                    templateRef: 'countNews',
                    context: {
                        countNews: topic.news
                    }
                },
                {
                    cellId: `cell-${this.cellCount - 1}`,
                    templateRef: 'totalNews',
                    context: {
                        totalNews: topic.total
                    }
                }
            ]
        };
    }

    // 更新表格的配置数据，tableConfig的body设置成当前页的数据
    private setUpTableBody() {
        this.tableConfig.body = [];
        const initIdx = (this.currentPage - 1) * this.pageSize;
        const lastIdx = this.currentPage * this.pageSize;
        this.tableConfig.body = this.allRows.slice(initIdx, lastIdx);
    }
}
