import { Component, OnInit, ViewChild, AfterViewInit } from '@angular/core';
import { DashboardService } from './dashboard.service';
import { DashboardQuery, DashboardFormQuery } from './api/dashboard-api.model';
import { NormalizeTime } from '../../services/NormalizeTime';
import { FilterFormComponent } from './components/filter-form/filter-form.component';
import { SDGNameDictionary } from '../model/sdg.enum';
import { EntityNameDictionary } from '../model/entity.enum';

@Component({
    selector: 'dashboard-component',
    templateUrl: 'dashboard.component.html'
})
export class DashboardComponent implements OnInit, AfterViewInit {
    public titleMainNews: string = 'Main News';
    public titleMostPolarizedNews: string = 'Most Polarized News';

    // 五个数据observable流
    public mostPolarizedNews$ = this.dashboardService.mostPolarizedNews$;
    public mainNews$ = this.dashboardService.mainNews$;
    public topics$ = this.dashboardService.topics$;
    public stats$ = this.dashboardService.stats$;
    public statsEntity$ = this.dashboardService.statsEntity$;
    public statsSDGDay$ = this.dashboardService.statsSDGDay$;

    @ViewChild(FilterFormComponent)
    private filterFormComponent: FilterFormComponent;

    constructor(private dashboardService: DashboardService, private normalizeTime: NormalizeTime) {}

    // 若store的data未加载过，加载默认数据
    public ngOnInit() {
        if (!this.dashboardService.hasDataset()) {
            this.loadInitRequest();
        }
    }

    public ngAfterViewInit(): void {
        if (this.dashboardService.hasDataset()) {
            this.loadLastQuetionForm();
        }
    }

    // 提交filter表单的查询条件，根据查询条件获取数据并保存查询条件
    public onSubmit(dashboardQuery: DashboardQuery) {
        this.dashboardService.loadDataset(dashboardQuery); // 加载数据
        this.dashboardService.saveLastFilter(dashboardQuery); // 保存最后的查询条件
    }

    // 页面初始数据加载（近30天的数据）
    private loadInitRequest(): void {
        const Day = 864e5;
        const nowDate = this.normalizeTime.toBackend(new Date(Date.now()));
        const yesterdayDate = this.normalizeTime.toBackend(new Date(Date.now() - Day * 30));
        const sdgCount = Object.keys(SDGNameDictionary).length;
        const ent = ['all'];
        Object.keys(EntityNameDictionary).forEach((k) => ent.push(k));

        const parameters = {
            dateFrom: yesterdayDate,
            dateTo: nowDate,
            sdg: Array.from({ length: sdgCount + 4 }, (item, index) => index),
            entity: ent,
            news_id: ''
        };

        this.dashboardService.loadDataset(parameters);
        this.dashboardService.saveLastFilter(parameters);
    }

    private loadLastQuetionForm(): void {
        const lastFilter = this.dashboardService.getLastFilter();
        if (!lastFilter) {
            return;
        }

        const filter: DashboardFormQuery = {
            ...lastFilter,
            dateTo: new Date(lastFilter.dateTo),
            dateFrom: new Date(lastFilter.dateFrom),
            entity: lastFilter.entity,
            sdg: lastFilter.sdg,
            news_id: lastFilter.news_id
        };

        this.filterFormComponent.loadForm(filter);
    }
}
