import { Component, Input, OnChanges, SimpleChanges, ViewChild } from '@angular/core';
import { SDGNameDictionary, SDGColorDictionary } from '../../../model/sdg.enum';
import { Thin2Chart } from 'thin2-chart';
import { StatisticsSDGDay } from '../../api/dashboard-api.model';

@Component({
    selector: 'bar-chart-component',
    templateUrl: './bar-chart.component.html'
})
export class BarChartComponent implements OnChanges {
    @ViewChild(Thin2Chart)
    public chart: Thin2Chart;

    @Input()
    public dataset: any[];
    private xAxisDates = []; // 无用
    private yAxisDataset = [];

    public ngOnChanges(changes: SimpleChanges): void {
        this.dataset = changes.dataset.currentValue;
        if (this.dataset) {
            this.xAxisDates = this.dataset.map(this.xAxisDatesAdapter);
            this.yAxisDataset = this.dataset.map(this.yAxisDatasetAdapter);
            this.drawChart();
        }
    }

    public drawChart() {
        const options = {
            chart: {
                type: 'column'
            },
            title: {
                text: ''
            },
            xAxis: {
                type: 'category',
                labels: {
                    rotation: -45,
                    style: {
                        fontSize: '13px',
                        fontFamily: 'Verdana, sans-serif'
                    }
                }
            },
            yAxis: {
                min: 0,
                title: {
                    text: 'Total News'
                }
            },
            legend: {
                enabled: false
            },
            tooltip: {
                pointFormat:
                    '<b>{point.sdgId}. {point.sdgName}</b> <br> <b>Total news: {point.y}</b>'
            },
            series: [
                {
                    name: '',
                    data: this.yAxisDataset,
                    dataLabels: {
                        enabled: true,
                        rotation: -90,
                        color: '#FFFFFF',
                        align: 'right',
                        format: '',
                        style: {
                            fontSize: '13px',
                            fontFamily: 'Verdana, sans-serif'
                        }
                    }
                }
            ]
        };

        this.chart.draw(options);
    }

    public xAxisDatesAdapter = (item: StatisticsSDGDay): string => {
        return item.day;
    };

    public yAxisDatasetAdapter = (item: StatisticsSDGDay) => {
        return {
            name: item.day,
            color: SDGColorDictionary[item.ods],
            y: Number(item.total),
            sdgId: item.ods,
            sdgName: SDGNameDictionary[item.ods]
        };
    };
}
