import { Injectable } from '@angular/core';

@Injectable({
    providedIn: 'root'
})
export class NormalizeTime {
    public toBackend(date: Date) {
        return `${date.getFullYear()}-${this.normalizeDateNumber(
            date.getMonth() + 1
        )}-${this.normalizeDateNumber(date.getDate())}`;
    }
    private normalizeDateNumber(value: number): string {
        return value < 10 ? `0${value}` : value.toString();
    }
}
